import {action, observable} from 'mobx';
import {message} from 'antd';
import request from '../common/service'
import qs from 'qs';
import {processJsonData} from '../common/apis'

class PredictCompareStore {

    @observable dataLeft = []; // 
    @observable dataRight = []; // 
    @observable loading = false; // loading

    @observable scorePrecision = 0; // 
    @observable scoreMAE = 0; // 
    @observable scoreMAPE = 0; // 
    @observable precisionClear = 0; // 
    @observable precisionSlow = 0; // 
    @observable precisionJam = 0; // 
    @observable precisionSlowJam = 0; // 
    
    // 
    // jsonhistorygtpred 
    @action async loaddata(jsonPath,type) {
        // 
        let data = {
            data: [],
            datatime: ''
        }
        let response = await fetch(jsonPath)
        let resData = await response.json();
        resData = resData.data
        if (resData === undefined) {
            message.warning('');
        }
        else {
            resData = processJsonData(resData)
            data.data = resData
            data.datatime = jsonPath.split('/')[jsonPath.split('/').length - 1].split('.')[0]
        }
        
        if(type === 'gt'){
            this.dataLeft = data
        }
        else if(type === 'pred'){
            this.dataRight = data
        }
        else{
            message.warning('loaddata param error');
        }
    }

    // historyGroundTruth
    @action async getHistoryGt(param){
        this.loading = true;
        const res = await request.get('data/history/gt' + '?' + qs.stringify(param))
        this.loading = false;
        if(res === undefined){
            message.error(' undefined')
        }
        else{
            let data = {
                data: [],
                datatime: ''
            } 
            let resData = res.data
            resData = processJsonData(resData) 
            data.data = resData
            data.datatime = res.jsonName
            this.dataLeft = data
            console.log(res)
        }

    }
    // history
    @action async getHistoryPred(param){
        this.loading = true;
        const res = await request.get('data/history/pred' + '?' + qs.stringify(param))
        this.loading = false;
        if(res === undefined){
            message.error(' undefined')
        }
        else{
            let data = {
                data: [],
                datatime: ''
            } 
            let resData = res.data
            resData = processJsonData(resData) 
            data.data = resData
            data.datatime = res.jsonName
            this.dataRight = data
            this.scorePrecision = res.scorePrecision
            this.scoreMAE = res.scoreMAE
            this.scoreMAPE = res.scoreMAPE
            this.precisionClear = res.precisionClear
            this.precisionSlow = res.precisionSlow
            this.precisionJam = res.precisionJam
            this.precisionSlowJam = res.precisionSlowJam
            console.log(res)
        }
    }
    
    // 
    // Lr
    @action async getPredLr(param){
        this.loading = true;
        const res = await request.get('data/predict/lr')
        this.loading = false;
        // 
        let data = {
            data: [],
            datatime: ''
        }
        if(res === undefined){
            message.error(' undefined')
        }
        else{
            let resData = res.data
            resData = processJsonData(resData)      
            data.data = resData
            data.datatime = res.jsonName
            this.dataLeft = data
            console.log(res)
        }
    }
    // Sage
    @action async getPredSage(param){
        this.loading = true;
        const res = await request.get('data/predict/sage')
        this.loading = false;
        // 
        let data = {
            data: [],
            datatime: ''
        }
        if(res === undefined){
            message.error(' undefined')
        }
        else{
            let resData = res.data
            resData = processJsonData(resData)      
            data.data = resData
            data.datatime = res.jsonName
            this.dataRight = data
            console.log(res)
        }
    }

    //  
    @action async clearAll(){
        this.dataLeft = []; // 
        this.dataRight = []; // 
    }
}


const predictCompareStore = new PredictCompareStore();
export default predictCompareStore;